

























import {Component, Prop, Vue} from "vue-property-decorator"
import * as TwilioVideo from "twilio-video"
import {IStudentProfile, ITrainerProfile, ITrainingSession, TrainingSessionState} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import Zendesk from "@/managers/zendesk.ts"
import TopBar from "@/components/TopBar.vue"
import PendingMessage from "@/components/PendingMessage.vue"
import TrainingSessionEnd from "@/components/TrainingSessionEnd.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { PendingMessage, TopBar, TrainingSessionEnd } })
export default class TrainingSession extends Vue {
	@Prop()
	public session!: ITrainingSession

	@Prop()
	public profile!: IStudentProfile | ITrainerProfile

	public timeLeft: string | null = null
	public clientTimeDelta: number = 0
	public duration!: number
	public listenCounter: number | null = null

	private showPreloader: boolean = true
	private msgWait: TranslateResult | null = i18n.t("trainingSession.text3")
	private isAudioDisabled: boolean = false
	private isVideoDisabled: boolean = false

	private room: TwilioVideo.Room | null = null
	private localTracks: TwilioVideo.Track[] | null = null

	private mediaErr: boolean = false
	private storage = window.localStorage

	private endSession: boolean | null = null
	private notificationMsg: string | null = null

	public created(): void {
		Portal.client.call.system.Time()
			.response
			.then(res => {
				this.clientTimeDelta = new Date(res.Time).getTime() - Date.now()
			})
	}

	public mounted(): void {
		// check browser video/audio
		this.checkBrowserMedia()
			.then(isMedia => {
				if (isMedia) {
					this.mediaErr = false
					// Zendesk session start
					if (this.storage.getItem("zendesk") === null) {
						this.storage.setItem("zendesk", "1")
						Zendesk.postTicketStart(this.profile, this.session)
					}
				} else {
					this.mediaErr = true
					Zendesk.postTicketMediaErr(this.profile, this.session)
				}
			})

		// initialize Twilio
		this.InitializeLocalTracks()
			.then(() => {
				const options: any = {name: this.session.Id, logLevel: "info"}

				if (this.localTracks !== null)
					options.tracks = this.localTracks

				TwilioVideo.connect(this.session.Token, options)
					.then(room => {
						this.room = room
						this.initializeRoom(room)

						// Start time counter
						this.countDown()

					}, reason => {
						console.log("Failed to connect: " + reason.toString()) // tslint:disable-line
					})
			})
			.catch(reason => {
				throw reason
			})
	}

	public destroyed(): void {
		if (this.room !== null) {
			this.room.disconnect()
			this.room = null
		}
		if (this.listenCounter !== null)
			clearTimeout(this.listenCounter)
	}

	public leaveSession(): void {
		this.endSession = true
	}

	/**
	 * Session cancel: on user click cancel button
	 * Session complete: time's up
	 */

	public cancel(): void {
		this.storage.removeItem("zendesk")
		Portal.client.call.trainingSession.cancel(this.session.Id)
			.response.then(() => this.$emit("ended", "cancel"))
	}

	public complete(): void {
		this.storage.removeItem("zendesk")
		if (Portal.isStudent)
			Portal.client.call.trainingSession.studentComplete(this.session.Id, null)
				.response.then(() => this.$emit("ended", "complete"))
		else
			Portal.client.call.trainingSession.trainerComplete(this.session.Id)
				.response.then(() => this.$emit("ended", "complete"))
	}

	/**
	 * Twilio init functions
	 */

	private InitializeLocalTracks(): Promise<TwilioVideo.Track[]> {
		const localTracksPromise = this.localTracks !== null
			? Promise.resolve(this.localTracks)
			: TwilioVideo.createLocalTracks()

		localTracksPromise
			.then(tracks => {
				this.localTracks = tracks
				const localContainer = this.$refs.local as HTMLElement
				if (!localContainer.querySelector("video"))
					this.attachTracks(tracks, localContainer)
		}, error =>	console.error("Unable to access local media", error)) // tslint:disable-line

		return localTracksPromise
	}

	private initializeRoom(room: TwilioVideo.Room): void {
		const remoteContainer = this.$refs.remote as HTMLElement

		// this.attachParticipantTracks(room.localParticipant, localContainer)
		room.participants.forEach(p => this.attachParticipantTracks(p, remoteContainer))

		room.on("trackSubscribed", (track, participant) => {
			this.attachTracks([track], remoteContainer)
			this.showPreloader = false
			this.msgWait = null
		})
		room.on("trackUnsubscribed", (track, participant) => this.detachTracks([track]))
		room.on("participantDisconnected", participant => {
			this.getSessionState()
				.then(state => {
					this.setWaitingMsg(state)
					if ([TrainingSessionState.Expired, TrainingSessionState.OtherUserCancelled, TrainingSessionState.OtherUserCompleted].indexOf(state) !== -1)
						this.detachParticipantTracks(participant)
				})
		})
		room.on("disconnected", () => {
			if (this.localTracks !== null)
				this.localTracks.forEach((t: any) => t.stop())
			this.localTracks = null
			this.detachParticipantTracks(room.localParticipant)
			room.participants.forEach(p => this.detachParticipantTracks(p))
			this.room = null
		})
	}

	private attachParticipantTracks(participant: TwilioVideo.Participant, container: HTMLElement) {
		const tracks = Array.from(participant.tracks.values())
		this.attachTracks(tracks, container)
	}

	private attachTracks(tracks: any[], container: HTMLElement): void {
		tracks.forEach(t => container.appendChild(t.attach()))
	}

	private detachParticipantTracks(participant: TwilioVideo.Participant) {
		const tracks = Array.from(participant.tracks.values())
		this.detachTracks(tracks)
	}

	private detachTracks(tracks: any[]) {
		tracks.forEach(t => t.detach().forEach((dE: HTMLMediaElement) => dE.remove()))
	}

	// Mute local audio track
	private disableAudio(): void {
		if (this.localTracks !== null) {
			this.isAudioDisabled = !this.isAudioDisabled;
			(this.localTracks[0] as any).isEnabled
				? (this.localTracks[0] as any).disable()
				: (this.localTracks[0] as any).enable()
		}
	}

	// Disable local video track
	private disableVideo(): void {
		if (this.localTracks !== null) {
			this.isVideoDisabled = !this.isVideoDisabled;
			(this.localTracks[1] as any).isEnabled
				? (this.localTracks[1] as any).disable()
				: (this.localTracks[1] as any).enable()
		}
	}

	/**
	 * Get sesion state and set appropriate message
	 */

	private getSessionState(): Promise<number> {
		return Portal.client.call.trainingSession.getOpen()
			.response.then(session => session.State)
	}

	// set appropriate message for user
	private setWaitingMsg(state: number): void {
		if (state === 1) {
			this.showPreloader = true
			this.msgWait = Portal.isStudent ? i18n.t("trainingSession.text4") : i18n.t("trainingSession.text5").toString()
		} else if (state === 5)
			this.msgWait = Portal.isStudent ? i18n.t("trainingSession.text6") : i18n.t("trainingSession.text7").toString()
		else if (state === 4 || state === 6)
			this.msgWait = i18n.t("trainingSession.text8")
	}

	/**
	 * CountDown timer
	 */

	private countDown() {
		this.calcTime()
		if (this.listenCounter !== null && this.duration <= 0) {
			clearTimeout(this.listenCounter)
			this.complete()
		} else
			this.listenCounter = window.setTimeout(() => this.countDown(), 1000)
	}

	private calcTime() {
		this.duration = new Date(this.session.ExpiresOn).getTime() - (Date.now() + this.clientTimeDelta)
		const m = Math.floor((this.duration / 1000 / 60) % 60)
		const s = Math.floor((this.duration / 1000) % 60)
		this.timeLeft = `${this.leadZero(m)}:${this.leadZero(s)} <span>Left</span>`

		// fix for "User not authenticated":
		// interact with api every 15min to reset the session timeout countdown
		if (m % 15 === 0 && s === 0)
			Portal.isStudent
				? Portal.client.call.student.me()
				: Portal.client.call.trainer.me()
	}

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`

	/**
	 * Check user's browser media (audio, video)
	 */

	private checkBrowserMedia(): Promise<boolean> {
		const constraints = {audio: true, video: true}
		return navigator.mediaDevices.getUserMedia(constraints)
			.then(() => true)
			.catch(() => false)
	}
}
