







import {Component, Prop, Vue} from "vue-property-decorator"
import PageBackground from "@/components/PageBackground.vue"

@Component({ components: { PageBackground } })
export default class PendingMessage extends Vue {
	@Prop({required: true})
	private msg!: string

	@Prop({type: Boolean})
	private showClose!: boolean

	public msgClose(): void {
		this.$emit("close")
	}
}
